import React, { Component } from 'react'

export default class Intro extends Component {
	render() {
		return (
			<div className="intro-section" id="home">
				<div className="container">
				  <div className="row align-items-center">
					<div className="col-lg-4 mr-auto" data-aos="fade-up">
					  <h1>Hi! I'm Denee.</h1>
					  <p className="mb-5">Benjamin Franklin once said, "If you would not be forgotten as soon as you are dead and rotten, either write things worth reading, or do things worth writing." Well, whether I'm forgotten or not, I want to write things worth reading. But I also want to write things worth <i>executing</i>. (See what I did there?) Check out my work and judge for yourself. Maybe we'll write something together.</p>
					  <p><a href="#contact-section" className="btn btn-outline-light py-3 px-5">Contact Me</a></p>
					</div>
					<div className="col-lg-2 ml-auto" data-aos="fade-up" data-aos-delay={100}>
					  <figure className="img-absolute">
						<img src="images/me_intro_resize_width.jpg" alt="Image" className="img-fluid" />
					  </figure>
					</div>
				  </div>
				</div>
			</div>
		)
	}
}