import React, { Component } from 'react'

export default class WritingSection extends Component {
	render() {
		return (
			<div className="site-section section-2" id="writing">
				<div className="container">
				  <div className="row">
					<div className="col-lg-6 mb-5">
					  <span className="section-sub-title d-block">Things Worth Reading</span>
					  <h2 className="section-title">Writing</h2>
					  <p>Visit the below links to read my published works.</p>
					</div>
				  </div>
				  <div className="row">
					<div className="col-lg-6" data-aos="fade-up" data-aos-delay>
					  <div className="process p-3">
						<span className="number">'16</span>
						<div>
						  <span className="flaticon-glasses display-4 text-primary mb-4 d-inline-block" />
						  <h3>Research</h3>
						  <p>The research paper <a href="https://knowledge.e.southern.edu/senior_research/188/">Artificially Intelligent Computer Assisted Language Learning System With AI Student Component</a> proposes an artifically intelligent language learning system that takes the usual roles of computer system as tutor and human user as student and flips them, making use of a critically acclaimed method of learning.</p>
						</div>
					  </div>
					</div>
					<div className="col-lg-6" data-aos="fade-up" data-aos-delay={200}>
					  <div className="process p-3">
						<span className="number">'17</span>
						<div>
						  <span className="flaticon-monitor display-4 text-primary mb-4 d-inline-block" />
						  <h3>Blog</h3>
						  <p><a href="https://delicatepages.blog/">Delicate Pages</a> is a weekly foray into the strange, beautiful, and mysterious words of the Bible. Never shy, never boring, always a new perspective.</p>
						</div>
					  </div>
					</div>
				  </div>
				</div>
			  </div>
		)
	}
}