import React, { Component } from 'react'

export default class AboutSection extends Component {
	render() {
		return (
			<div className="site-section section-1">
				<div className="container">
				  <div className="row">
					<div className="col-lg-5 mr-auto mb-5">
					  <div className="mb-5">
						<span className="section-sub-title d-block">About Me</span>
						<h2 className="section-title">Black. Adventist. Creative.</h2>
						<p>I am a self-taught writer and a university-taught coder. I graduated from Southern Adventist University with a <b>Bachelor's of Science in Computer Science</b> in 2016 and from the University of Notre Dame with a <b>Master's in Data Science</b> in 2020. I was published for the first time in 2011 in <i>Insight Magazine</i>.</p>
					  </div>
					</div>
					<div className="col-lg-6">
					  <div className="image-absolute-box">
						<div className="box" data-aos="fade-up">
						  <div className="icon-wrap"><span className="flaticon-vector" /></div>
						  <h3>What I'm Up To</h3>
						  <p>I currently work as a full-stack web developer and maintain a weekly blog on the side.</p>
						</div>
						<img src="images/me_writing.jpg" alt="Image" className="img-fluid" />
					  </div>
					</div>
				  </div>
				</div>
			  </div>
		)
	}
}