import React, { Component } from 'react'

export default class Header extends Component {
	render() {
		return (
			<header className="site-navbar py-4 js-sticky-header site-navbar-target" role="banner">
				<div className="container-fluid">
				  <div className="d-flex align-items-center">
					<div className="site-logo"><a href="index.html">&lt;denee&gt; &lt;writes&gt; &lt;codes&gt;</a></div>
					<div>
					  <nav className="site-navigation position-relative text-right" role="navigation">
						<ul className="site-menu main-menu js-clone-nav mr-auto d-none d-lg-block">
						  <li><a href="#home" className="nav-link">Home</a></li>
						  <li><a href="#writing" className="nav-link">Writing</a></li>
						  <li><a href="#coding" className="nav-link">Coding</a></li>
						</ul>
					  </nav>
					</div>
					<div className="ml-auto">
					  <nav className="site-navigation position-relative text-right" role="navigation">
						<ul className="site-menu main-menu site-menu-dark js-clone-nav mr-auto d-none d-lg-block">
						  <li className="cta"><a href="#contact-section" className="nav-link"><span className="rounded border border-primary">Contact</span></a></li>
						</ul>
					  </nav>
					  <a href="#" className="d-inline-block d-lg-none site-menu-toggle js-menu-toggle text-black float-right"><span className="icon-menu h3" /></a>
					</div>
				  </div>
				</div>
			  </header>
		)
	}
}